@font-face {
  font-family: "Podkova";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Podkova-Regular.woff2") format("woff2"), url("../fonts/Podkova-Regular.woff") format("woff");
}
@font-face {
  font-family: "Podkova";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Podkova-Bold.woff2") format("woff2"), url("../fonts/Podkova-Bold.woff") format("woff");
}
@font-face {
  font-family: "Roboto Mono";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Regular.woff2") format("woff2"), url("../fonts/RobotoMono-Regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto Mono";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMono-Bold.woff2") format("woff2"), url("../fonts/RobotoMono-Bold.woff") format("woff");
}
html,
body {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

body {
  position: relative;
  max-height: 100%;
  width: 100%;
  font-family: "Roboto Mono", monospace;
  font-size: 14px;
  line-height: 30px;
  color: #eeeeee;
  background-color: #1b1c23;
}

.content, .promo, .about-me {
  will-change: transform;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

a {
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

h2 {
  position: relative;
  font-family: "Podkova", serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 53px;
  color: #f9d24a;
}
@media (max-width: 524px) {
  h2 {
    font-size: 42px;
    line-height: 46px;
  }
}

.button {
  padding: 24px 30px;
  width: 250px;
  font-weight: 700;
  font-size: 20px;
  line-height: 26px;
  font-family: "Roboto Mono", monospace;
  border: 0;
  border-radius: 10px;
  text-align: center;
  background-color: inherit;
  cursor: pointer;
}

.container {
  padding: 0 100px;
  margin: 0 auto;
  max-width: 1440px;
}
@media (max-width: 1250px) {
  .container {
    padding: 0 50px;
  }
}

.overlay {
  position: fixed;
  top: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header {
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 1;
}
.page-header::after {
  content: "";
  position: absolute;
  width: 100%;
  top: -105px;
  height: 255px;
  background-image: linear-gradient(180deg, rgba(27, 28, 35, 0.5) 61.88%, rgba(27, 28, 35, 0) 93.09%);
  z-index: -1;
}

@media (max-width: 767px) {
  .page-header--open .page-header__container {
    background-color: #1b1c23;
    opacity: 0.97;
  }
  .page-header--open .page-header__list {
    position: absolute;
    display: flex;
    flex-direction: column;
    padding: 43px 20px;
    width: 100%;
    left: 0;
    top: 85px;
    background-color: #1b1c23;
  }
  .page-header--open .page-header__list li {
    margin-bottom: 26px;
  }
  .page-header--open .page-header__x-circle {
    display: flex;
  }
  .page-header--open .page-header__burger-svg {
    display: none;
  }
  .page-header--open::after {
    display: none;
  }
}

.page-header__container {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 15px;
}

.page-header__burger {
  display: none;
  padding: 0;
  border: none;
  background-color: inherit;
  cursor: pointer;
  z-index: 50;
}
@media (max-width: 767px) {
  .page-header__burger {
    display: block;
  }
}

.page-header img {
  z-index: 10;
  border-radius: 50%;
}

.page-header__x-circle {
  display: none;
}

.page-header__burger-svg {
  display: none;
}
@media (max-width: 767px) {
  .page-header__burger-svg {
    display: flex;
  }
}

.page-header__list {
  display: flex;
  justify-content: space-between;
  width: 595px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .page-header__list {
    display: none;
  }
}

.page-header a {
  position: relative;
  padding: 0 7px;
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  color: #eeeeee;
  text-decoration: none;
}
.page-header a::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 5%;
  display: block;
  height: 2px;
  width: 0;
  background-color: #eeeeee;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
}
.page-header a:hover::after, .page-header a:focus::after {
  width: 100%;
  left: 0;
  cursor: pointer;
}

.promo {
  position: relative;
  padding-top: 277px;
  width: 100%;
  height: 1023px;
  background-image: url("../img/background.jpg");
  background-position: 50%;
  background-size: cover;
  background-repeat: no-repeat;
  opacity: 0.95;
}
.promo::after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: -3px;
  height: 255px;
  background-image: linear-gradient(to bottom, transparent, #1b1c23);
  z-index: -1;
}
@media (min-width: 1600px) {
  .promo {
    height: 1225px;
    background-position: 0 0;
  }
}
@media (max-width: 767px) {
  .promo {
    background-image: url("../img/background-mobile.jpg");
  }
  .promo::before {
    display: none;
  }
}

.promo p:first-child {
  margin: 0 0 30px;
  font-weight: 400;
  font-size: 24px;
  line-height: 32px;
}
@media (max-width: 524px) {
  .promo p:first-child {
    margin: 0 0 20px;
  }
}

.promo__subtitle {
  margin: 0 0 109px;
  font-weight: 700;
  font-size: 32px;
  line-height: 42px;
  color: #f9d24a;
}
@media (max-width: 767px) {
  .promo__subtitle {
    width: 60%;
  }
}
@media (max-width: 524px) {
  .promo__subtitle {
    width: 100%;
    font-size: 25px;
    line-height: 36px;
  }
}

.promo h1 {
  margin: 0 0 12px;
  font-family: "Podkova", serif;
  font-weight: 700;
  font-size: 48px;
  line-height: 53px;
}
@media (max-width: 524px) {
  .promo h1 {
    font-size: 33px;
    line-height: 24px;
  }
}

.promo___resume {
  position: relative;
  left: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 27px 30px;
  margin-right: 39px;
  color: #1b1c23;
  background-image: linear-gradient(180deg, #f9d24a 0%, #c19700 100%);
  text-shadow: 0 2px 3px rgba(0, 0, 0, 0.25);
  text-decoration: none;
}
.promo___resume:hover, .promo___resume:focus {
  background-image: linear-gradient(-45deg, #f9d24a 0%, #c19700 100%);
  transition: 0.5s;
}
.promo___resume:hover svg, .promo___resume:focus svg {
  fill: rgba(238, 238, 238, 0.75);
}
@media (max-width: 767px) {
  .promo___resume {
    display: flex;
    margin-bottom: 25px;
  }
}
@media (max-width: 524px) {
  .promo___resume {
    padding: 19px 22px;
    width: 210px;
    font-size: 17px;
    line-height: 24px;
  }
}

.promo___resume svg {
  position: absolute;
  right: 15px;
  fill: #1b1c23;
}
@media (max-width: 524px) {
  .promo___resume svg {
    width: 30px;
    height: 30px;
  }
}

.promo___certificates {
  color: #f9d24a;
  border: 3px solid #f9d24a;
}
.promo___certificates:hover, .promo___certificates:focus {
  background: rgba(238, 238, 238, 0.23);
  color: #ffffff;
  transition: 0.5s;
}
@media (max-width: 524px) {
  .promo___certificates {
    padding: 19px 15px;
    width: 210px;
    font-size: 17px;
    line-height: 24px;
  }
}

.about-me {
  display: grid;
  grid-template-columns: 655px 330px;
  justify-content: space-between;
  padding-bottom: 85px;
  padding-top: 30px;
}
@media (max-width: 1023px) {
  .about-me {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 1023px) {
  .about-me__content {
    margin-bottom: 15px;
  }
}

.about-me__content p:last-child {
  display: flex;
  align-items: center;
}

.about-me__content a {
  color: #c19700;
  text-decoration: none;
}
.about-me__content a:hover, .about-me__content a:focus {
  color: rgba(249, 210, 74, 0.7);
  transition: 0.5s;
}

.about-me__location-item {
  margin-right: 5px;
  width: 30px;
  height: 30px;
}

.about-me img {
  margin-top: 75px;
  border-radius: 50%;
}
@media (max-width: 1023px) {
  .about-me img {
    margin-top: 25px;
    width: 262px;
    height: 262px;
  }
}
@media (max-width: 767px) {
  .about-me img {
    object-fit: cover;
  }
}

.about-me h2 {
  margin: 0 0 35px;
}

.about-me p {
  font-size: 21px;
  line-height: 29px;
  color: #eeeeee;
}
@media (max-width: 767px) {
  .about-me p {
    font-size: 18px;
    line-height: 27px;
  }
}
@media (max-width: 524px) {
  .about-me p {
    font-size: 15px;
    line-height: 25px;
  }
}

.projects {
  padding-top: 40px;
}

.projects h2 {
  margin-top: 0;
  margin-bottom: 35px;
}

.projects__list {
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  margin: 0;
  padding: 0;
  list-style: none;
}

.projects__item {
  position: relative;
}

.projects__mask {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  z-index: 5;
  transform: scale(0);
}

.projects__item:hover .projects__mask {
  display: flex;
  transform: scale(1);
  transition: 0.5s;
}

.projects__link {
  display: block;
  width: 270px;
  margin: 19% auto;
  padding: 25px 25px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  text-align: center;
  border: 2px solid rgba(51, 51, 51, 0.7);
  border-radius: 10px;
  background-color: rgba(17, 17, 17, 0.5);
}
@media (max-width: 1440px) {
  .projects__link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    padding: 17px;
    width: 100%;
    height: 100%;
  }
}

.projects__list img {
  width: 599px;
  height: 396px;
  border-radius: 10px;
  object-fit: cover;
}
@media (max-width: 1440px) {
  .projects__list img {
    width: 469px;
    height: 306px;
  }
}
@media (max-width: 1070px) {
  .projects__list img {
    width: 396px;
    height: 260px;
  }
}

.projects__info {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 500;
  color: #2c2e3f;
  background-color: #ffffff;
  width: 100%;
  height: 68px;
  padding: 15px;
  text-align: center;
  font-size: 14px;
  line-height: 19px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
}
@media (max-width: 767px) {
  .projects__info {
    top: 80px;
    padding: 13px 15px 28px;
    color: #ffffff;
    font-size: 14px;
    line-height: 18px;
    background-image: linear-gradient(180deg, rgba(27, 28, 35, 0.62) 0%, rgba(27, 28, 35, 0.5) 100%, rgba(27, 28, 35, 0) 100%);
    transform: inherit;
    padding-bottom: 11px;
  }
}
@media (max-width: 524px) {
  .projects__info {
    top: 65px;
    width: 290px;
  }
}

.projects__info p {
  margin: 0;
}

@media (max-width: 1065px) {
  .project__hidden {
    display: none;
  }
}

.contact-me {
  display: grid;
  grid-template-columns: 260px 762px;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .contact-me {
    grid-template-columns: 1fr;
  }
}

.contact-me__social {
  grid-column: 1/2;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 1023px) {
  .contact-me__social {
    margin-bottom: 20px;
  }
}

.contact-me__social li {
  margin-bottom: 22px;
}

.contact-me__social a {
  position: relative;
  display: flex;
  align-items: center;
  text-decoration: none;
  width: max-content;
  padding: 0 7px;
}
.contact-me__social a::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 5%;
  display: block;
  height: 2px;
  width: 0;
  background-color: #eeeeee;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
}
.contact-me__social a:hover::after, .contact-me__social a:focus::after {
  width: 100%;
  left: 0;
  cursor: pointer;
}

.contact-me__social svg {
  margin-right: 20px;
}
@media (max-width: 524px) {
  .contact-me__social svg {
    width: 20px;
    height: 16px;
  }
}

.contact-me__social span {
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  color: #eeeeee;
}
@media (max-width: 524px) {
  .contact-me__social span {
    font-size: 18px;
    line-height: 23px;
  }
}

.contact-me__info {
  position: relative;
  display: flex;
}

.contact-me__info picture {
  order: 1;
}
@media (max-width: 524px) {
  .contact-me__info picture {
    display: flex;
    align-items: self-end;
  }
}

.contact-me__info p span {
  margin-top: 10px;
  display: block;
}

.contact-me__info p {
  position: relative;
  margin-top: 0;
  padding: 20px 24px;
  height: min-content;
  width: 392px;
  background-color: #292a30;
  border: 3px solid #f9d24a;
  border-radius: 30px;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: #eeeeee;
  text-shadow: 0 2px 4px #000000;
}
.contact-me__info p::after {
  content: "";
  position: absolute;
  right: -23px;
  bottom: -7px;
  width: 40px;
  height: 21px;
  background-image: url("../img/svg/direction.svg");
  background-repeat: no-repeat;
  transform: rotate(2deg);
}
@media (max-width: 767px) {
  .contact-me__info p {
    top: 30px;
    padding: 10px 9px;
    width: 42%;
    font-size: 15px;
    line-height: 22px;
  }
}
@media (max-width: 524px) {
  .contact-me__info p {
    top: 0;
    width: 49%;
    font-size: 11px;
    line-height: 15px;
    padding: 11px 12px 8px;
  }
  .contact-me__info p::after {
    right: -18px;
    bottom: -1px;
    background-image: url("../img/svg/direction-mobile.svg");
    width: 34px;
    height: 14px;
    transform: rotate(-1deg);
  }
}

@media (max-width: 524px) {
  .contact-me__info img {
    width: 137px;
    height: 180px;
  }
}

.certificates {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  min-height: 100%;
  width: 100%;
  z-index: 100;
  display: none;
  justify-content: center;
  align-items: flex-start;
  overflow-y: scroll;
}
.certificates--open {
  display: flex;
}

.certificates__container {
  position: relative;
  max-width: 1315px;
  padding: 82px 25px;
}
@media (max-width: 1440px) {
  .certificates__container {
    max-width: 820px;
    padding: 82px 0;
  }
}

.certificates__button {
  position: absolute;
  right: -25px;
  top: 15px;
  width: 45px;
  height: 45px;
  padding: 0;
  border: none;
  background-color: inherit;
  cursor: pointer;
}
@media (max-width: 1440px) {
  .certificates__button {
    right: -50px;
  }
}
@media (max-width: 767px) {
  .certificates__button {
    right: -10px;
  }
}
@media (max-width: 524px) {
  .certificates__button {
    width: 30px;
    height: 30px;
    top: 21px;
    right: -13px;
  }
}

@media (max-width: 524px) {
  .certificates__button svg {
    width: 20px;
    height: 20px;
  }
}

.certificates__list {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 0;
  margin: 0;
  list-style: none;
  z-index: 10;
}
@media (max-width: 1440px) {
  .certificates__list {
    gap: 15px;
  }
}
@media (max-width: 524px) {
  .certificates__list {
    width: 290px;
  }
}